import AppInfo from '../app-info/app-info';
import SearchPanel from '../search-panel/search-panel';
import AppFilter from '../app-filter/app-filter';
import EmployeesList from '../employees-list/employees-list';
import EmployeesAddForm from '../employees-add-form/employees-add-form';
import './app.css';
import { Component } from 'react';

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: [
        { name: 'Max', salary: '2000', increase: true, rise: true, id: 1 },
        { name: 'Dan', salary: '1000', increase: false, rise: false, id: 2 },
        { name: 'Kar', salary: '100', increase: false, rise: false, id: 3 },
      ],
      term: '',
      filter: 'all'
    }
    this.maxId = 4
  }

  deleteEmploye = (id) => {
    this.setState(({ data }) => ({
      data: data.filter(item => item.id !== id)
    }))
  }

  addEmploye = (name, salary) => {
    const newEmploye = {
      name,
      salary,
      increase: false,
      rise: false,
      id: this.maxId++
    }

    this.setState(({ data }) => {
      const newData = [...data, newEmploye]
      return {
        data: newData
      }
    })
  }

  onToggleProp = (id, prop) => {
    this.setState(({ data }) => ({
      data: data.map(item => {
        if (item.id === id) {
          return { ...item, [prop]: !item[prop] }
        }
        return item
      })
    }))
  }

  searchEmp = (items, term) => {
    if (term.length === 0) {
      return items
    }

    return items.filter(item => {
      return item.name.indexOf(term) > -1
    })
  }

  onInputSearch = (term) => {
    this.setState({ term })
  }

  filterPost = (items, filter) => {
    switch (filter) {
      case 'rise':
        return items.filter(item => item.rise)
      case '1000':
        return items.filter(item => item.salary > 1000)
      default:
        return items
    }
  }

  onFilterSelect = (filter) => {
    this.setState({ filter })
  }


  render() {
    const { data, term, filter } = this.state
    const employee = this.state.data.length
    const increased = this.state.data.filter(item => item.increase).length
    const visibleData = this.filterPost(this.searchEmp(data, term), filter)
    return (
      <div className="app" >
        <AppInfo employee={employee} increased={increased} />

        <div className="search-panel">
          <SearchPanel onInputSearch={this.onInputSearch} />
          <AppFilter filter={filter} onFilterSelect={this.onFilterSelect} />
        </div>

        <EmployeesList
          data={visibleData}
          deleteEmploye={this.deleteEmploye}
          onToggleProp={this.onToggleProp}
        />
        <EmployeesAddForm addEmploye={this.addEmploye} />
      </div>
    );
  }
}

export default App;